@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.header__description {
  font-size: 12px;
}

.jumbo {
  background-image: url(/static/media/bmw.fc490ac3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 20px;
  color: whitesmoke;
  padding-left: 55px;
}

.jumbo__title {
  font-size: 55px;
}

.jumbo__text {
  font-size: 20px;
}

.searchBar__search {
  padding-left: 25px;
  padding-right: 25px;
}

.catalog {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 30px;
}

.footer {
  width: 100%;
  background-color: rgb(92, 92, 112);
  color: whitesmoke;
  height: 200px;
  bottom: 0;
  margin-top: 50px;
  padding: 30px;
}

.footer__link {
  text-decoration: none;
  color: whitesmoke;
}

.footer__link:hover {
  text-decoration: none;
  color: white;
}

.links {
  margin-left: 20px;
}

.movieCard {
  width: 320px;
  min-width: 320px;
  margin: 20px;
}

